.centre {
  display: table-cell; vertical-align: bottom;
}

.buttonBox {
  height: 50px;
  margin: 25px;
}

.buttonBox {
  text-align: center;
  width: 100%;
}

.box-1 {

  max-width: auto;
  text-align: left;
  padding-bottom: 25px;
  min-height: 200px;
}

.box-2 {

  width: auto;
  text-align: left;
  padding-bottom: 25px;
  min-height: 100px;
  position: relative;
  left: 0%;
}

.box-3 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;


}

.box-4 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;


}

.box-5 {

  height: 200px;

}

.box-6 {

  height: 75px;
  text-align: right;
  align-items: center;

}



.box-8 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;


}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding-bottom: 10px;
}


.button {
  left: 50%;
  
  align-items: center;
  background-color: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: .25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  display: inline-flex;
  font-family: system-ui,-apple-system,system-ui,"Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.userInput {
  width: 400px;

}




